.Toolkitpage{
    width: 100%;
    height: 100%;
    position:relative;
    display: flex;
    flex-direction: column;
    margin:  0 auto;
    background-position: center;
    background-size: 100% 100%;
    background-image: url(../../Img/Toolback.png);
}
.Toolkittitle{
    margin: 0 auto;
    padding: 40px;
    height: 40px;
    font-size: 40px;
    font-family: "J";
    text-align: center;
    display: flex;
}
.Toolkitpagebtn{
    width:40px;
    height: 40px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(../../Img/ToolkitBtn.png);
}
.ToolkitpageGrid{
    width: 95%;
    height: 600px;
    margin: 0 auto;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(8,1fr);
}

.ToolkitpageGridbtn{
    padding: 3%;
    font-size: 25px;
    font-family: "SB";
    text-align: center;
    border: 1px solid #005CDC;
    background-color: #ECF9FF;
    color: #005CDC;
}

.ToolkitpageGridbtnA{
    padding: 3%;
    font-size: 25px;
    font-family: "SB";
    text-align: center;
    border: 1px solid #ECF9FF;
    background-color: #005CDC;
    color: #ECF9FF;
}

.ToolkitpageGridbtnR{
  padding: 3%;
  font-size: 25px;
  font-family: "SB";
  text-align: center;
  border: 1px solid #FFBBC6;
  background-color: #FFD4D7;
  color: red;
}

.ToolkitpageText{
    padding:  20px 40px;
    width: 95%;
    height: 30px;
    font-size: 30px;
    font-family: "S";
    display: flex;
}

.ToolkitpageText2{
  padding: 10px 40px;
  width: 95%;
  height: 20px;
  font-size: 20px;
  font-family: "S";
  display: flex;
}

.ToolkitpageResult{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ToolkitpageGridS{
    width: 95%;

    margin: 5px auto;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(6, 1fr);
}
.ToolkitpageInput{
    margin: 0 20px;
    width: 950px;
    height: 35px;
    font-size: 30px;
    font-family: "SB";
    border: 1px solid #000;
    text-align: center;
    border-radius: 20px;
    background-color: #f4f4f4;
}
.ToolkitpageTextArea{
    margin: 0 auto;
    padding: 10px;
    width: 93%;
    height: 250px;
    font-size: 30px;
    font-family: "SB";
    border: 1px solid #000;
    text-align: center;
    border-radius: 20px;
    background-color: #f4f4f4;
    word-break: keep-all;
    overflow: auto;
    line-height: 42px;
}
.ToolkitpageGridCardCheck{
    width: 95%;
    height: 280px;
    margin: 0 auto;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(auto-fill, minmax(15%, auto));
