.Toolkitfinishpage{
  width: 100%;
  height: 250%;
  position:relative;
  display: flex;
  flex-direction: column;
  margin:  0 auto;
  background-position: center;
  background-size: 100% 100%;
  background-image: url(../../Img/Toolkitfinishback.png);
}

  .ToolkitfinishTitle{
    font-family: "J";
    font-size:45px;
    color: #5835E5;
    text-shadow: -3px 0 #fff, 0 3px #fff, 3px 0 #fff, 0 -3px #fff;
    text-align: center;
    padding-top: 40px;
  }


.ToolkitfinishpageText{
  padding:  20px 40px;
  width: 95%;
  height: 35px;
  font-size: 30px;
