  .Login{
    width: 400px;
    height: 400px;
    position:absolute;
    top:45%;
    left: 63%;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-size: 100% 100%;
  }
   
  .Logininput{
    font-family: 'J';
    margin-top: 3%;
    width: 300px;
    height: 50px;
    font-size:20px;
    outline: none;
    border: 5px solid #5835E5;
    padding: 1%;
    border-radius: 30px;
    text-align: center;
  }
  
  .Loginbtn{
    font-family: 'J';
    color: #fff;
    font-size:20px;
    margin:  2% 0 1% 0;
    width: 150px;
    height: 55px;
    background-color: #5835E5;
    border: 5px solid #fff;
    border-radius: 100px;
    position:relative;
    text-align: center;
    transition:800ms ease all;
    outline:none;
  }

  
  .Usertitle{
