  .Cardbox{
    padding: 0 100px 0 80px;
    width: 620px;
    height: 550px;
    display: grid;
    gap:10px;
    grid-template-columns: repeat(3, minmax(200px, auto));
    grid-template-rows: repeat(2,250px);
  }
  
  .CardText_content{
    width: 125px;
    padding: 3%;
    margin: 8px auto;
    color:#000;
    font-family: 'SB';
    font-size: 14px;
    text-align: center;
