  .Availtybox{
    width: 95%;
    height: 32%;
    margin: 3% auto 4%;
    position: relative;
    display: flex;
  }
  
  .AvailtyButton_content{
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
  }
  
  .AvailtyButton_effect{
    width: 60%;
    height: 60%;
    border: none;
    background: transparent;
    animation: rotate-vert-center 2s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
    margin: 50px auto 30px;
  }
  
  .AvailtyButton_text{
    width: 100%;
    height: 40%;
    text-align: center;
    color:tomato;
    animation: blink-1 1s infinite both;
    margin: 0 auto;
    font-family: "J";
    font-size: 25px;
  }
  
  
  .Availty_content{
    width: 100%;
    height: 100%;
    display: grid;
    padding: 0.05%;
    gap:1.5%;
    grid-template-columns: repeat(6, minmax(14%, auto));
    grid-template-rows: repeat(1,100%);
  }
  
  
  .Noavailty{
    font-family: "J";
    font-size:40px;
    color: #fff;
    margin: 7% auto;
    text-shadow: -3px 0 black, 0 3px black, 3px 0 black, 0 -3px black;
  }
  
  .Warning{
    width: 80px;
    height: 100px;
    margin: 0 auto;
    background-position: center;
    background-size: 100% 100%;
    background-image: url(../../Img/warning.png);
    animation: rotate-vert-center 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
  }
  @keyframes rotate-vert-center {
    0% {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
    }
    100% {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
  }
  
  .Result{
    color:tomato;
    animation: blink-1 0.6s infinite both;
    margin: 0 1%;
  }
  @keyframes blink-1 {
    0%,
    50%,
    100% {
      opacity: 1;
    }
    25%,
    75% {
      opacity: 0;
    }
  }
  
  .Ending{
    width: 1340px;
    height: 840px;
    margin: 0 auto;
    z-index: 1;
    background-position: center;
    background-size: 100% 100%;
    background-image: url(../../Img/ending.png);
    animation: fade-in 10s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  }
  
  @keyframes bounce-out-top {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
    }
    5% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    15% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
    }
    25% {
      -webkit-transform: translateY(-38px);
              transform: translateY(-38px);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    38% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
    }
    52% {
      -webkit-transform: translateY(-75px);
              transform: translateY(-75px);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    70% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
    }
    85% {
      opacity: 1;
    }
    100% {
      -webkit-transform: translateY(-800px);
              transform: translateY(-800px);
      opacity: 0;
    }
  }
  
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  .Log{
    width:360px;
    height: 110px;
    background-color: rgba(0, 0, 0, .3); 
    position: absolute;
    top:50%;
    left: 65%;
    overflow: scroll;
  }
  
  .Log_text{
    word-wrap: break-word;
    text-align: left;
    color: #fff;
    font-family: "SB";
    margin: 5px;
  }
  
  
  .titles{
    position:relative;
    text-align: center;
    padding-top: 50px;
  }
  
  .typing-txt{
    position:relative;
    font-size: 40px;
    width: 100%;
    font-family: "KO";
    height: 50px; 
    overflow: hidden;
    animation: typingAni 3s steps(5, end)  1;
  } 
  .typing-txt::after{
    position: absolute; 
    display: block;
    content: ""; 
    width: 100%;
    height: 50px; 
    font-family: "KO";
    top: 0;
    right: 0; 
    border-right: 1px solid #000;
    animation: cursor 0.5s step-end infinite;
  }
  @keyframes typingAni {
    0%{width:0%}
    100%{width: 100%};
  }
  @keyframes cursor{
    0%{opacity:0;}
    50%{opacity:1;}
    100%{opacity:0;}
  }

  .hearbeatEffect{
    animation: heartbeat 1.5s ease-in-out infinite both;
  }