.ToolkitAfterpage{
    width: 95%;
    height: 85%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background-position: center;
    background-size: 100% 100%;
    background-image: url(../../Img/ToolAfterback.png);
    padding: 60px 30px;
    position:relative;
    margin:  0 auto;
  }

  .CSATpageText{
    padding: 10px;
    width: 98%;
    height: 10px;
    font-size: 16px;
    font-family: "SB";
    display: flex;
    margin: 20px 0 0 0 ;
  }
  
.CSATpageGridS{
    width: 98%;
    height: 98%;
    margin: auto;
  }

  .CSATpageInput{
    margin: 0 20px;
    width: 100px;
    height: 25px;
    font-size: 20px;
    font-family: "SB";
    border: 1px solid #000;
    text-align: center;
    background-color: #f4f4f4;
  }
  
  .CSATpageTextArea{
    margin: 10px 0 0 0;
    padding: 10px;
    width: 580px;
    height: 130px;
    font-size: 15px;
    font-family: "S";
    border: 1px solid #000;
    text-align: center;
    border-radius: 20px;
    word-break: keep-all;
  }

.CSATpagebtn{
    padding: 10px;
    width: 70px;
    height: 15px;
    font-size: 15px;
    font-family: "SB";
    text-align: center;
    border: 1px solid #005CDC;
    background-color: #ECF9FF;
    color: #005CDC;
  }